$xs: 480px;
$sm: 768px;
$md: 820px;
$lg: 1024px;
$xl: 1200px;
$xxl: 1400px;

@mixin xs {
  @media screen and (max-width: $xs) {
    @content;
  }
}

@mixin sm {
  @media screen and (max-width: $sm) {
    @content;
  }
}

@mixin md {
  @media screen and (max-width: $md) {
    @content;
  }
}

@mixin lg {
  @media screen and (max-width: $lg) {
    @content;
  }
}

@mixin xl {
  @media screen and (max-width: $xl) {
    @content;
  }
}

@mixin xxl {
  @media screen and (max-width: $xxl) {
    @content;
  }
}